import './Signup.css';
import { useFormik } from 'formik';
import * as Yup from "yup";

function Signup() {

  const formik = useFormik({
    onSubmit: values => {
      console.log("submit");
    },
    validationSchema: Yup.object().shape({
      email: Yup.string().email("Invalid Email address").required("Required"),
      first_name: Yup.string().required("Required"),
      last_name: Yup.string().required("Required"),
      password: Yup.string().min(8, "Must be 8 characters or more").required("Required"),
      passwordConfirmation: Yup.string().test('passwords-match', 'Passwords must match', function(value){
        return this.parent.password === value
      })
    }),
  });

  return (
    <div id="signupBox">
      <form className="form" onSubmit={formik.handleSubmit}>
          <div className="input-group">
            <label htmlFor="first_name">First Name</label>
            <input type="text" name="first_name" placeholder="John" value={formik.values.first_name}/>
          </div>
          <div className="input-group">
            <label htmlFor="last_name">Last Name</label>
            <input type="text" name="last_name" placeholder="Doe" value={formik.values.last_name}/>
          </div>
          <div className="input-group">
            <label htmlFor="email">Email</label>
            <input type="email" name="email" placeholder="johndoe@email.com"  value={formik.values.email}/>
          </div>
          <div className="input-group">
            <label htmlFor="password">Password</label>
            <input type="password" name="password"  value={formik.values.password}/>
          </div>
          <div className="input-group">
            <label htmlFor="confirmPassword">Confirm Password</label>
            <input type="password" name="confirm_password"  value={formik.values.confirm_password}/>
          </div>
          <button className="primary">Submit</button>
        </form>
    </div>
  );
}

export default Signup;